import React, {useEffect, useMemo, useState} from 'react';
import './App.css';
import {useDispatch, useSelector} from "react-redux";
import {getArticlesTC} from "./bll/articles-reducer";
import {AppRootStateType} from "./bll/store";
import {ArticlesType} from "./dal/api";
import Article from "./components/Article";
import Search from "./components/Search";

function App() {
    const [searchTerm, setSearchTerm] = useState("")
    const dispatch = useDispatch()
    const articles = useSelector<AppRootStateType, Array<ArticlesType>>(state => state.artilcles)

    let searchedArticles = useMemo(() => {
        let searched: Array<ArticlesType> = [...articles]
        searched = searchTerm ? searched.filter(item => item.title.toLowerCase().includes(searchTerm.toLowerCase())) : searched
        return searched
    }, [articles, searchTerm])

    useEffect(
        () => {
            dispatch(getArticlesTC({_limit: 100}))
        },[]
    )
    return (

        <div className="App">
            <Search setSearchTerm={setSearchTerm}/>
            <div className={"articles__wrapper"}>
                {searchedArticles.map(article => { return <Article
                    id={article.id}
                    title={article.title}
                    imageUrl={article.imageUrl}
                    summary={article.summary}
                    publishedAt={article.publishedAt}
                    search={searchTerm}

                />})}
            </div>


        </div>
    );
}

export default App;
